<template>
  <div class="details">
    <van-swipe :autoplay="3000" indicator-color="white">
      <van-swipe-item>1</van-swipe-item>
      <van-swipe-item>2</van-swipe-item>
      <van-swipe-item>3</van-swipe-item>
      <van-swipe-item>4</van-swipe-item>
    </van-swipe>

    <div class="commodity-introduce">
      <div class="commodity-left">
        <div class="commodity-name textOverFlow">
          <span style="font-weight: bold">name</span>
        </div>
        <div style="font-size:12px;color:#666;margin-top: 1px;">
          这里是......
        </div>
      </div>
    </div>

    <div class="details-price">￥168</div>

    <div class="details-content">
      <div class="details-title">details-title</div>
      <div class="details-introduce">
        哈哈哈哈哈哈哈哈哈啊哈哈哈哈啊哈哈哈哈哈哈
      </div>
    </div>

    <div class="recommend-content">
      <div class="details-title">recommend-content</div>
      <ul>
        <li v-for="(item,index) in 3" @click="goDetails(index)">
          <div class="commodity-img"></div>
          <div class="commodity-introduce">
            <div class="commodity-left">
              <div class="commodity-name textOverFlow">
                name
              </div>
              <div style="font-size:12px;color:#666;margin-top: 1px;">
                168元/斤
              </div>
            </div>
            <div class="card-shopping">
              <van-icon name="shopping-cart-o"  size="1.2rem"  color ="red" style="margin-top: 0.2rem;"/>
            </div>
          </div>
        </li>
      </ul>
    </div>

    <!--sku-->
    <van-sku
      v-model="showBase"
      :sku="sku"
      :goods="goods"
      :goods-id="goodsId"
      :hide-stock="sku.hide_stock"
      :quota="quota"
      :quota-used="quotaUsed"
      :custom-stepper-config="customStepperConfig"
      @buy-clicked="onBuyClicked"
      @add-cart="onAddCartClicked"
    />

    <van-goods-action>
    <van-goods-action-mini-btn
      icon="chat-o"
      text="客服"
      @click="onClickMiniBtn"/>

    <van-goods-action-mini-btn
      icon="cart-o"
      text="购物车"
      @click="onClickMiniBtn"/>

    <van-goods-action-big-btn
      text="加入购物车"
      @click="onClickBigBtn"/>

    <van-goods-action-big-btn
      primary
      text="立即购买"
      @click="onClickBigBtn"/>

    </van-goods-action>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        showBase:false,
        goodsId:'',
        quota:0,
        quotaUsed:2,
        customStepperConfig: {
          // 自定义限购文案
          // quotaText: '0',
          // 自定义步进器超过限制时的回调
          handleOverLimit: (data) => {
            const { action, limitType, quota, quotaUsed } = data;

            if (action === 'minus') {
              this.$toast('至少选择一件商品');
            } else if (action === 'plus') {
              // const { LIMIT_TYPE } = Sku.skuConstants;
              if (limitType === LIMIT_TYPE.QUOTA_LIMIT) {
                let msg = `单次限购${quota}件`;
                if (quotaUsed > 0) msg += `，你已购买${quotaUsed}`;
                this.$toast(msg);
              } else {
                this.$toast('库存不够了');
              }
            }
          }
        },
        sku: {
          // 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
          // 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
          tree: [
            {
              k: '颜色', // skuKeyName：规格类目名称
              v: [
                {
                  id: '1193', // skuValueId：规格值 id
                  name: '红色', // skuValueName：规格值名称
                  imgUrl: 'https://img.yzcdn.cn/2.jpg' // 规格类目图片，只有第一个规格类目可以定义图片
                },
                {
                  id: '1215',
                  name: '蓝色',
                  imgUrl: 'https://img.yzcdn.cn/2.jpg'
                }
              ],
              k_s: 's2', // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
            },
            {
              k: '尺寸',
              v: [
                {
                  id: '1193',
                  name: '1',
                },
                {
                  id: '1215',
                  name: '1',
                }
              ],
              k_s: 's1', // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
            },
          ],
          // 所有 sku 的组合列表，比如红色、M 码为一个 sku 组合，红色、S 码为另一个组合
          list: [
            {
              id: 2259, // skuId，下单时后端需要
              price: 100, // 价格（单位分）
              s1: '1215', // 规格类目 k_s 为 s1 的对应规格值 id
              s2: '1193', // 规格类目 k_s 为 s2 的对应规格值 id
              stock_num: 110 // 当前 sku 组合对应的库存
            }
          ],
          price: '1.00', // 默认价格（单位元）
          stock_num: 227, // 商品总库存
          collection_id: 2261, // 无规格商品 skuId 取 collection_id，否则取所选 sku 组合对应的 id
          none_sku: false, // 是否无规格商品
          messages: [
            {
              // 商品留言
              datetime: '0', // 留言类型为 time 时，是否含日期。'1' 表示包含
              multiple: '1', // 留言类型为 text 时，是否多行文本。'1' 表示多行
              name: '备注', // 留言名称
              type: 'text', // 留言类型，可选: id_no（身份证）, text, tel, date, time, email
              required: '1' // 是否必填 '1' 表示必填
            }
          ],
          hide_stock: false // 是否隐藏剩余库存
        },
        goods: {
          title: '测试商品',
          picture: 'https://img.yzcdn.cn/2.jpg'
        },
      }
    },
    methods:{
      onClickMiniBtn() {
        this.showBase=true
      },
      onClickBigBtn() {
        this.showBase=true
      },
      onBuyClicked(){
          this.$router.push({path:'./shoppingCar'})
      },
      onAddCartClicked(){


      }
    }
  }
</script>

<style scoped lang="scss">
  .details{
    background: #F1F1F1;
    padding-bottom: 4rem;
    .van-swipe{
      width: 100%;
      height: 12rem;
      background: antiquewhite;
      line-height: 12rem;
    }
    .details-price{
      color:#FC3049;
      font-size: 14px;
      padding: 0 0.8rem 0.5rem 0.8rem;
      text-align: left;
      background: #fff;
    }
    .commodity-img{
      height: 9rem;
      background: bisque;
      border-radius: 2%;
      box-sizing: border-box;
    }
    .recommend-content>ul{
      display: flex;
      flex-wrap:wrap;
      justify-content: space-between;
    }
    .recommend-content>ul>li{
      width: 10.5rem;
      min-height: 12rem;
      box-shadow: 0 0 8px 0 #999, 0 2px 4px 0 rgba(232,237,250,.5);
      box-sizing: border-box;
      border-radius: 2%;
      margin-top: 1rem;
    }
    .commodity-introduce{
      color:#000;
      text-align: left;
      padding: 0.5rem  0.8rem;
      display: flex;
      box-sizing: border-box;
      background: #fff;
      .commodity-left{
        flex: 1;
      }
    }
    .details-title{
      padding: 0.3rem 0;
      font-size: 30px;
      font-weight: bold;
    }
    .details-content{
      padding: 0.5rem  0.8rem;
      background: #fff;
      margin-top: 0.5rem;
      text-align: left;
    }
    .recommend-content{
      margin-top: 0.5rem;
      padding: 0.5rem  0.8rem;
      background: #fff;
      text-align: left;
    }
  }
</style>



